* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primaryBackground: #212222;
  --secondaryBackground: #2f3030;
  --primaryColor: #e0a80d;
  --greyDark: #7d8792;
  --headingColor: #2d2e2e;
  --white: #fff;
  --black: #000;
  --sliver: #bac8d3;
}

body {
  background-color: var(--headingColor);
  color: var(--white);
  font-family: Poppins, sans-serif;
}

a {
  color: var(--greyDark);
  text-decoration: none;
  transition: all .5s;
}

a:hover {
  color: var(--primaryColor);
}

p {
  color: var(--sliver);
  margin: 20px 0;
  font-size: 15px;
  line-height: 30px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}

.container {
  max-width: 100%;
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.flexsb {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

button {
  background: none;
  background-color: var(--primaryColor);
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 50px;
  outline: none;
  margin-top: 20px;
  padding: 15px 50px;
  font-size: 17px;
}

.fontSize {
  font-size: 20px;
}

.primaryBtn {
  border: 2px solid var(--primaryColor);
  background: none;
}

header {
  background-color: var(--headingColor);
  z-index: 999;
  width: 100%;
  padding-top: 20px;
  line-height: 10vh;
  position: sticky;
  top: 0;
  left: 0;
}

.logo {
  padding-top: 10px;
}

header a {
  margin-left: 40px;
}

.toggle {
  cursor: pointer;
  color: var(--white);
  display: none;
}

.logo img {
  width: 50px;
  height: 50px;
}

@media screen and (width <= 768px) {
  header .nav {
    display: none;
  }

  header a {
    color: var(--white);
    width: 100%;
    display: block;
  }

  .toggle {
    background: none;
    margin: 0;
    font-size: 25px;
    display: block;
  }

  .hideMenu {
    background-color: var(--headingColor);
    width: 100%;
    display: block;
    position: absolute;
    top: 10vh;
    left: 0;
  }
}

.shimmer-loader {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.shimmer-item {
  background: #313030;
  border-radius: 4px;
  width: 100%;
  height: 150px;
  position: relative;
  overflow: hidden;
}

.shimmer-item:before {
  content: "";
  background: linear-gradient(to right, #2b1a1a00 0%, #2b282833 50%, #1d1b1b00 100%);
  width: 100%;
  height: 100%;
  animation: 1.5s infinite shimmer;
  position: absolute;
  top: 0;
  left: -100%;
}

@keyframes shimmer {
  0% {
    left: -100%;
  }

  100% {
    left: 100%;
  }
}

section {
  align-items: center;
  display: flex;
}

.flex {
  display: flex;
}

.hero {
  background-color: #313030;
  margin-top: 20px;
  position: relative;
}

.hero:after {
  content: "";
  z-index: -1;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 90vh;
  position: absolute;
  top: 0;
  left: 0;
}

.heroContent {
  text-align: center;
  max-width: 50%;
  margin: 46px auto auto;
}

.hero h1 {
  color: var(--primaryColor);
  font-size: 60px;
  font-weight: 600;
}

.heading {
  color: var(--primaryColor);
  text-align: center;
  margin-bottom: 60px;
  font-size: 30px;
  font-weight: 600;
}

@media screen and (width <= 768px) {
  .about-section {
    flex-direction: column;
  }
}

.about .primaryBtn {
  margin-left: 20px;
}

.about .heading {
  text-align: left;
  margin: 0;
}

.left {
  width: 35%;
}

.right {
  width: 65%;
}

.about-section {
  justify-content: space-evenly;
  width: 100%;
}

.contact {
  margin: 20px 0;
}

.contact .right, .contact .left {
  padding: 20px;
}

.contact .flex input:first-child {
  margin-right: 10px;
}

.contact button, .contact textarea, .contact input {
  border: none;
  border-radius: 5px;
  outline: none;
  width: 100%;
  margin: 10px 0;
  padding: 20px;
}

.grid3 {
  grid-gap: 30px;
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.products {
  text-align: center;
  color: var(--primaryColor);
  margin-top: 10px;
  font-size: 50px;
}

.card {
  background-color: #313030;
  box-shadow: 0 1px 3px #0000001f, 0 1px 2px #0000003d;
}

.card-body {
  padding: 0 20px;
}

.cv-btn {
  margin-bottom: 30px;
}
